import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Productcard from '../Components/Productcard'
import Dropdownbox from '../Components/Dropdownbox';
import Sidebar from '../Components/Adminsidebar';


import Loading from './Loading';
import { useSearchParams } from 'react-router-dom';


export default function Products({ isAdmin }) {
    let [products, setProducts] = useState()
    const [searchParams, setSearchParams] = useSearchParams()
    let filter = searchParams.get('category') || searchParams.get('brand')

    useEffect(() => {
        getData(filter)

    }, [])

    useEffect(() => {
        getData(filter)
    }, [filter])
    const getData = async (filter) => {
        const urlSearchParams = new URLSearchParams(window.location.search);
        const params = Object.keys(Object.fromEntries(urlSearchParams.entries()));
        // isAdmin()
        console.log(params.includes("brand"))
        if (filter) {
            let dta
            if (params.includes("category")) {
                let { data } = await axios.get(`https://skincare-92zr.onrender.com/product/all?category=${filter}`)
                dta = data.message
            }
            else if (params.includes("brand")) {
                let { data } = await axios.get(`https://skincare-92zr.onrender.com/product/all?brand=${filter}`)
                dta = data.message
            }

            setProducts(dta)

        } else {
            let { data } = await axios.get("https://skincare-92zr.onrender.com/product/all")
            setProducts(data.message)
        }
    }
    const getSearchData = async (e) => {
        if (e.key == "Enter") {
            console.log(e.target.value)

            let { data } = await axios.get(`https://skincare-92zr.onrender.com/product/search?name=${e.target.value}`)
            console.log(data)
            setProducts(data.message)

        }
    }
    const deleteProduct = async (e) => {
        console.log(e.target.id)
        let dlt = await axios.delete(`https://skincare-92zr.onrender.com/product/dlt/${e.target.id}`)
        console.log(dlt)
        getData()

    }
    return (<div className='container py-2 pt-5'>
        <div className='d-flex align-items-stretch'>
            {/* <input type="text" className=' form-control border border-end-0' placeholder='search' onKeyDown={(e) => { getSearchData(e) }} /> */}
            {/* <button className='bg-pink broder border-0 rounded rounded-1' ><i class="fa-solid fa-magnifying-glass"></i></button> */}
            {/* <div>
                <Dropdownbox />
            </div> */}
        </div>
        <div className='d-flex justify-content-start align-items-stretch flex-wrap'>
            {products ? <>{products.length ? products.map((ele, index) => {
                return Object.keys(ele).length === 0 && ele.constructor === Object ? <></> : <Productcard props={ele} key={index} func={deleteProduct} isAdmin={isAdmin} />
            }) : <div className='w-100 h-100 d-flex justify-content-center align-items-center fw-bold fs-1 alert alert-secondary'>No Available products</div>}</> : <Loading />}</div></div>

    )
}
