import React from 'react'
// import img from '../vlcsnap-2023-02-04-13h35m29s448.png'
import { useNavigate } from 'react-router-dom'
import axios from 'axios'
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';
export default function Productcard({ props, func, isAdmin }) {
    const navto = useNavigate()

    let brandname=props.brand.replace("_"," ")
    return (
        <div className='p-1 p-sm-3 col-xl-3 col-md-4 col-sm-6 col-xs-6 '>
            <div className='rounded rounded-3 col-12 card-bg d-flex flex-column  position-relative h-100'>
                {props.isAvailable ? <></> : <div className='position-absolute h-100 top-0 col-12 rounded rounded-3 out d-flex justify-content-center align-items-center' onClick={() => { navto(`/prod/${props._id}`) }}>
                    <p className='text-light fw-bold'>OUT OF STOCK</p>
                </div>}
                <div className='position-absolute w-100 d-flex justify-content-end p-3 dlt '>
                    {isAdmin ? <><i class="fa-solid fa-pen-to-square me-2 fs-5" onClick={() => { navto(`/prodForm/update/${props._id}`) }}></i>
                        <Popup trigger=
                            {<i class="fa-solid fa-trash text-danger make-cursor fs-5" ></i>}
                            modal nested>
                            {
                                close => (


                                    <div className='alert alert-danger h-100'><p>you are about to delete this product</p>
                                        <div className='d-flex w-100 justify-content-center'>
                                            <button className='btn btn-danger me-2' id={props._id} onClick={(e) => { func(e); close() }}>Delete</button>
                                            <button className='btn btn-light' onClick={() => { close() }}>Cancel</button>
                                        </div>
                                    </div>


                                )
                            }
                        </Popup></> : <></>}
                </div>
                <div className='col-12 d-flex justify-content-center align-items-center img-div'>
                    {props.images[0] ? <img src={props.images[0].secure_url} alt="" className='col-12 product-img rounded rounded-3 h-100' /> :
                        <i class="fa-regular fa-image"></i>}
                </div>
                <div className='p-sm-4 pt-sm-2 d-flex flex-column h-100 justify-content-between '>
                    <div className='d-flex justify-content-between align-items-center flex-wrap p-1'>
                        {/* <div> */}
                        {/* <p className='fw-light m-0 brand'>brand</p> */}
                       {props.brand?<p className='m-0'><span className='fs-xs-6 text-secondary fw-light'><small>{brandname}</small></span></p>:<p className='m-0'><span className='fs-xs-6 text-secondary fw-light'><small>not specified brand</small></span></p>}
                        <p className=' fs-6 lh-1 make-cursor fw-bold' onClick={() => { navto(`/prod/${props._id}`) }}><small className='fs-xs-6'>{props.prodName}</small></p>
                        {/* </div> */}


                    </div>

                    <div className='p-1'>
                        <div>
                            <i class="fa-solid fa-cart-shopping make-cursor icon me-2 fs-5"></i>
                            <i class="fa-solid fa-heart-circle-plus make-cursor icon fs-5"></i>
                        </div>

                        <button className='product-btn col-12 rounded rounded-5 py-2 px-1 border border-1  d-flex align-items-center justify-content-around' onClick={() => { navto(`/prod/${props._id}`) }}>
                            {/* <p className='mb-0 pink-text fs-6 ' ><small>show product</small></p> */}
                            {props.newPrice ? <p className='mb-0 pink-text fs-6 '><small className='p-1 fs-xs-6'>EGP {props.newPrice}</small></p> : <p className='mb-0 pink-text fs-6 '><small className='p-1 fs-xs-6'>EGP {props.price}</small></p>}
                        </button>
                    </div>

                </div>
            </div>
        </div>
    )
}
